import React, { useState, useEffect, useRef } from 'react';
import { OpenVidu } from 'openvidu-browser';

function VideoCall() {
    const [session, setSession] = useState(null);
    const [publisher, setPublisher] = useState(null);
    const [subscribers, setSubscribers] = useState([]);
    const [sessionId, setSessionId] = useState('');
    const [isConnected, setIsConnected] = useState(false);

    const publisherRef = useRef(null);
    const subscribersRef = useRef([]);

    //  
    const createSession = async () => {
        try {
            const response = await fetch('http://localhost:8081/api/sessions', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({}) //   
            });

            if (response.ok) {
                const newSessionId = await response.text();
                setSessionId(newSessionId);
                console.log(' :', newSessionId);
                return newSessionId;
            } else {
                throw new Error('  ');
            }
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    //  
    const getToken = async (sessionId) => {
        try {
            const response = await fetch(`http://localhost:8081/api/sessions/${sessionId}/connections`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({}) //   
            });

            if (response.ok) {
                const token = await response.text();
                console.log(' :', token);
                return token;
            } else {
                throw new Error('  ');
            }
        } catch (error) {
            console.error('  :', error);
            throw error;
        }
    };

    //  
    const joinSession = async () => {
        try {
            // 1.  
            const newSessionId = await createSession();
            if (!newSessionId) return;

            // 2. OpenVidu  
            const OV = new OpenVidu();
            const mySession = OV.initSession();

            // 3.   
            mySession.on('streamCreated', (event) => {
                console.log('  ');
                const subscriber = mySession.subscribe(event.stream, undefined);
                setSubscribers(prev => [...prev, subscriber]);

                //   DOM 
                setTimeout(() => {
                    if (subscribersRef.current[subscribers.length]) {
                        subscriber.addVideoElement(subscribersRef.current[subscribers.length]);
                    }
                }, 100);
            });

            mySession.on('streamDestroyed', (event) => {
                console.log(' ');
                setSubscribers(prev => prev.filter(sub => sub !== event.stream.streamManager));
            });

            setSession(mySession);

            // 4.    
            const token = await getToken(newSessionId);
            await mySession.connect(token);

            // 5.    
            const myPublisher = OV.initPublisher(undefined, {
                audioSource: undefined,
                videoSource: undefined,
                publishAudio: true,
                publishVideo: true,
                resolution: '1280x720',
                frameRate: 30,
                insertMode: 'APPEND',
                mirror: false
            });

            // 6.   
            await mySession.publish(myPublisher);
            setPublisher(myPublisher);
            setIsConnected(true);

            // 7.   DOM 
            setTimeout(() => {
                if (publisherRef.current) {
                    myPublisher.addVideoElement(publisherRef.current);
                }
            }, 100);

            console.log('  !');

        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    //  
    const leaveSession = () => {
        if (session) {
            session.disconnect();
            setSession(null);
            setPublisher(null);
            setSubscribers([]);
            setIsConnected(false);
            setSessionId('');
            console.log(' ');
        }
    };

    return (
        <div style={{ padding: '20px' }}>
            <h1>MOIM </h1>

            {!isConnected ? (
                <div>
                    <button
                        onClick={joinSession}
                        style={{
                            padding: '10px 20px',
                            fontSize: '16px',
                            backgroundColor: '#007bff',
                            color: 'white',
                            border: 'none',
                            borderRadius: '5px',
                            cursor: 'pointer'
                        }}
                    >
                         
                    </button>
                </div>
            ) : (
                <div>
                    <button
                        onClick={leaveSession}
                        style={{
                            padding: '10px 20px',
                            fontSize: '16px',
                            backgroundColor: '#dc3545',
                            color: 'white',
                            border: 'none',
                            borderRadius: '5px',
                            cursor: 'pointer',
                            marginBottom: '20px'
                        }}
                    >
                         
                    </button>

                    <div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
                        {/*   */}
                        <div>
                            <h3> </h3>
                            <video
                                ref={publisherRef}
                                autoPlay
                                muted
                                style={{
                                    width: '320px',
                                    height: '240px',
                                    backgroundColor: '#000',
                                    border: '2px solid #007bff'
                                }}
                            />
                        </div>

                        {/*    */}
                        {subscribers.map((subscriber, index) => (
                            <div key={index}>
                                <h3> {index + 1}</h3>
                                <video
                                    ref={el => subscribersRef.current[index] = el}
                                    autoPlay
                                    style={{
                                        width: '320px',
                                        height: '240px',
                                        backgroundColor: '#000',
                                        border: '2px solid #28a745'
                                    }}
                                />
                            </div>
                        ))}
                    </div>

                    <p style={{ marginTop: '20px', color: '#666' }}>
                         ID: {sessionId}
                    </p>
                </div>
            )}
        </div>
    );
}

export default VideoCall;
